<template>
    <div class="list">
      <el-main>
        <el-table
          :data="tableData"
          stripe
          style="width: 100%"
          @selection-change="selectionChange"
        >
          <el-table-column
            prop="select"
            label=""
            width="50"
            show-overflow-tooltip
            type="selection"
          >
          </el-table-column>
          <el-table-column
            prop=""
            label="#"
            width="100"
            type="index"
            :index="formatter"
            show-overflow-tooltip
          >
          </el-table-column>
          <el-table-column
            prop="title"
            label="描述"
            width="200"
            color="red"
            show-overflow-tooltip
          >
            <template slot-scope="scope">
              <el-button type="text" @click="toDetails(scope.row._id)" style="color:#409EFF;
  text-decoration: underline;">{{scope.row.title}}</el-button>
            </template>
          </el-table-column>
          <el-table-column
            prop="status"
            label="状态"
            width="150"
            show-overflow-tooltip
          >
            <template slot-scope="scope">
              <el-tag type="success" v-if="scope.row.status == 1">进行中</el-tag>
              <el-tag type="danger" v-if="scope.row.status == 2">已结束</el-tag>
              <el-tag type="info" v-if="scope.row.status == 0">未进行</el-tag>
            </template>
          </el-table-column>
          <el-table-column
            prop="createTime"
            label="时间"
            width="200"
            show-overflow-tooltip
          >
          </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button
                size="mini"
                type="primary"
                @click="FormVisible = true ;edit(scope.row._id)">编辑</el-button>
              <el-dialog title="编辑" :visible.sync="FormVisible">
                <el-form :model="formData">
                  <el-form-item label="描述" >
                    <el-input v-model="formData.title" auto-complete="off"></el-input>
                  </el-form-item>
                  <el-form-item label="状态" >
                    <el-switch
                      v-model="formData.status"
                      active-color="#13ce66"
                      inactive-color="#ff4949">
                    </el-switch>
                  </el-form-item>
                  <el-form-item label="">
                    <div>内容</div>
                    <editor v-model="formData.desc"></editor>
                  </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                  <el-button @click="cancel">取 消</el-button>
                  <el-button type="primary" @click="updateReview">确 定</el-button>
                </div>
              </el-dialog>
              <el-button
                size="mini"
                type="danger"
                @click="del(scope.row._id)">删除</el-button>
              <!--开启或结束民主评议-->
              <el-button size="mini" type="success" v-if="scope.row.status == 2 || scope.row.status == 0" @click="open(scope.row._id)">开启</el-button>
              <el-button size="mini" type="danger" v-if="scope.row.status == 1"  @click="close(scope.row._id)">结束</el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-main>
      <el-footer>
        <el-button type="primary" @click="toggleSelection(multiSelection)">批量删除</el-button>
        <el-pagination
          background
          layout="prev, pager, next,jumper"
          :total="total"
          :current-page="page"
          :page-size="pageSize"
          @current-change="pageChange"
        >
        </el-pagination>
      </el-footer>
    </div>
</template>
<script>
    import editor from '../../components/wangeditor.vue'
    export default{
      name: "",
      components:{
        editor
      },
      data(){
          return{
            //选择器
            multiSelection:[],
            reviewId:[],
            //
            tableData:[],
            //弹框
            formData: {
              title:'',
              desc:'',
              status:'',
            },
            FormVisible: false,
            //wangeditor
            editorInit: false,
            //分页器
            pageSize:5,
            page:1,
            total:10
          }
      },
      methods:{
        //获取民主评议列表
        getReview(){
          this.$axios.get('getReview').then( res => {
            this.tableData = res.data
          })
        },
        //编辑
        edit(id){
          this.$router.push({path:'/review',query:{id} })
//          this.FormVisible= true
          //数据回填
          this.$axios.get('getReview',{id:this.$route.query.id}).then(res => {
          this.formData = res.data;
          })
        },
        //确认修改
        updateReview(){
          this.$nextTick(() => {
            this.editorInit = true;
          });
          let params = {
            ...this.formData,
            id:this.$route.query.id
          }
          this.$axios.post('updateReview',params).then( res => {
            if(res.code == 200){
              this.$message({
                message:`${res.msg}`,
                type:'success'
              })
              //关闭弹框
              this.FormVisible= false;
              setTimeout( () => {
                this.$router.push('/review')
                this.$router.go(0)
              },1000)
            }else{
              this.$message({
                message:`${res.msg}`,
                type:'warning'
              })
            }
          })
        },
        cancel(){
          this.FormVisible = false
          setTimeout( () => {
            this.$router.push('/review')
          },1000)
        },
        //删除
        del(id){
          console.log(id)
          this.$confirm('此操作将永久删除该内容, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            this.$axios.post('delReview', {id}).then(res => {
              if(res.code == 200){
                this.$message({
                  type: 'success',
                  message: '删除成功!'
                });
                setTimeout( () => {
                  this.$router.go(0)
                },1000)
              }else{
                this.$message({
                  type: 'error',
                  message: '删除失败!'
                });
              }
            })
          }).catch(() => {
            this.$message({
              type: 'warning',
              message: '已取消删除'
            });
          })
        },
        //开启和结束民主评议
        open(id){
          this.$axios.post('updateStatus', {status:1, id}).then( res => {
            if(res.code == 200){
              this.$message({
                type: 'success',
                message: '开启成功!'
              });
              setTimeout( () => {
                this.$router.go(0)
              },1000)
            }else{
              this.$message({
                type: 'error',
                message: '已经有一个已经开启的民主评议了!'
              });
            }
          })
        },
        close(id){
            this.$axios.post('updateStatus', {status:2, id}).then( res => {
              if(res.code == 200){
                this.$message({
                  type: 'success',
                  message: '关闭成功!'
                });
                setTimeout( () => {
                  this.$router.go(0)
                },1000)
              }else{
                this.$message({
                  type: 'error',
                  message: '关闭失败!'
                });
              }
            })
        },
        //批量选择
        selectionChange(val){
          this.multiSelection = val
//          console.log(val)
        },
        toggleSelection(rows){
          if(rows){
            for(var key in rows){
//                  console.log(key)
//                this.userId = rows[key]._id
              this.reviewId.push(rows[key]._id)
            }
//            console.log(this.userId)
            this.$axios.post('delManyReview', {id:this.reviewId}).then( res => {
              if(res.code == 200) {
                this.$message({
                  type:'success',
                  message:'批量删除成功'
                })
                setTimeout( () => {
                  this.$router.go(0)
                },1000)
              }
            })
          }else {
            this.$message({
              type:'warning',
              message:'没有选择'
            })
          }
        },
        //底部分页器
        //这个时候利用formatter就可以实现索引累加啦
        formatter(index){
          //放回索引
          return index + (this.page -1) *this.pageSize +1
        },
        pageChange(val){
//          console.log(`当前页: ${val}`);
          this.page = val
          this.$axios.get('getReview',{page:val, pageSize:this.pageSize}).then( res => {
            this.tableData = res.data
          })
        },
        //获取数据库文档总数
        getNumber(){
          this.$axios.get('getReviewNum').then( res => {
            this.total = res.data
          })
        },
        //单元格编辑
        toDetails(id){
          this.$router.push({path:'/review/perSummary',query:{id} })
        }
      },
      created(){
          this.getReview();
          this.getNumber()
      }
    }
</script>
<style scope>
  .list{
    width: 100%;
    height:100%;
    margin-right: 22px;
    padding-left: 20px;
    padding-top: 20px;
  }
/*内容区*/
  .el-main{
    padding:0 0 20px 0;
  }
  .list .el-table{
    border:1px solid #dfe7ed;
  }
.list .el-table .has-gutter .cell{
  color:#000;
}

  /*底部*/
  .list .el-footer{
    /*position: fixed;*/
    /*min-height: 500px;*/
    /*bottom: 20px;*/
    /*right:20px;*/
    position:relative;
    height: 56px !important;
    /*margin:0 22px 0 0;*/
    padding:0;
    background-color: #f1f1f1 !important;
  }
  .list .el-button{
    margin:10px 0 0 10px;
  }
  .list .el-footer .el-pagination{
    display: inline-block;
    position:absolute;
    top:12px;
    right:15px;
  }
  .list .el-footer .el-pagination.is-background .btn-next, .el-pagination.is-background .btn-prev, .el-pagination.is-background .el-pager li{
    background-color: #ffffff;
  }
</style>



